@import "var.sass";
@import "norm.sass";

.input_wrap {
	margin-top: 100px; }

@mixin boxshadow {
	-webkit-box-shadow: 0px 5px 10px 0px rgba(68,68,68,0.5);
	-moz-box-shadow: 0px 5px 10px 0px rgba(68,68,68,0.5);
	box-shadow: 0px 5px 10px 0px rgba(68,68,68,0.5); }

@mixin gradient {}

input {
	width: 320px;
	height: 70px;
	padding-right: 50px;
	padding-left: 50px;
	border-radius: 5px;
	margin-left: -10px;
	background-color: #FFF;
	cursor: pointer;
	outline: -webkit-focus-ring-color auto 0px;
	white-space: pre-wrap;
	@include boxshadow; }

input::first-line {
	font-size: 2em; }

.input_wrap::before {
	content: url(../img/lock.png);
	position: relative;
	left: 50px;
	top: 20px; }

a {
	display: inline-block;
	width: 320px;
	height: 70px;
	background-color: green;
	border-radius: 5px;
	text-align: center;
	padding-top: 5px;
	margin-top: 20px;
	@include boxshadow;
	margin-left: 20px;
	text-decoration: none;
	span {
	 	text-transform: uppercase; } }

a::before {
	content: url(../img/lock.png);
	position: relative;
	left: -20px;
	top: 20px; }

